#root {
  width: 100%;
  height: 100%;
  font-family: 'Computer Modern Serif', 'Noto Serif CJK KR', serif;
}

p {
  margin: .4rem 0!important;
}

#ace-editor {
  font-family: 'Computer Modern Typewriter', monospace;
  font-size: 0.95rem;
  line-height: 1.4em;
}

#renderer a {
  font-family: 'Computer Modern Typewriter', monospace;
}

#react-select-3-input {
  font-family: 'Computer Modern Typewriter', monospace;
}

.katex {
  font-size: 1.05rem !important;
}

#separator {
  height: calc(100% - 3rem);
  background-color: #2d3748;
  width: 6px;
  position: fixed;
  top: 3rem;
  left: -3px;
  z-index: 99;
  transition: width 0.14s cubic-bezier(0.4, 0, 0, 0.2), left 0.14s cubic-bezier(0.4, 0, 0, 0.2);
}

#separator:hover {
  width: 12px;
  left: -6px;
  cursor: pointer;
}

.Dropdown-control {
  border-radius: .5rem !important;
  padding: 4px 52px 4px 8px !important;
}

.Dropdown-arrow {
  top: 12px !important;
}

.hastebin-button {
  font-family: 'Computer Modern Typewriter', monospace;
  color: white;
  padding: .2rem .75rem;
  height: 2rem;
  line-height: 2rem;
  border-radius: 2rem;
  cursor: pointer;
  background-color: rgba(255, 255, 255, 0.11);
  width: 11rem;
  margin-left: 1rem;
  text-align: center;
}

.hastebin-button:hover {
  background-color: rgba(255, 255, 255, 0.25);
}